// Variables Colors

$color-primary-back: #D7FFF1;
$color-primary-light: #8CD790;
$color-primary-medium: #77AF9C;
$color-primary-dark: #285943;

$color-white: #ffffff;
$color-black: #000000;

$color-grey-dark: #777777;
$color-grey-dark-2: #999999;
$color-grey-dark-3: #333333;

$color-grey-light-1: #f7f7f7;
$color-grey-light-2: #eeeeee;

// Gradient Primary

$gradient-primary: linear-gradient(to right, rgba($color-primary-light, .8), rgba($color-primary-dark, .8));

// Font-size

$default-font-size: 1.6rem;
